@media only screen and (max-width: 600px) {
    .main-image{
        video{
          height: 80vh;  
          object-fit: cover;
        }

        .main-logo{
            .internal-text-two{
                font-size: 3vw;
                padding: 0;
                margin-right: 1em;
            }
        }
        
    }

    .quote{
        //background-color: rosybrown;
        flex-direction: column;
        

        .video-wrap,.right-text,.left-text{
            
            width: 100%;
            font-size: 3vw;
            div,video{
                width: 80%;
                height: 80%;
                object-fit: cover;
            }

            video{
                align-self: flex-end;
            }

            .right-text div{
                align-self: flex-start;
            }
            
        }

    }

    .three{

        .video-wrap video{
            align-self: flex-start;
        }

        .left-text{
            display: flex;
            div{
                align-self: flex-end;
            }
            
        }
    
    }




  }